.profile__subtitle {
    margin: 0;
    padding: 0;
    max-width: 336px;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

@media screen and (max-width: 767px) {
.profile__subtitle {
    max-width: 282px;
    font-size: 14px;
    line-height: 17px;
    text-align: center;
}
}