    font-weight: 400;
    src: url("./Inter/Inter-Regular.woff2") format("woff2"),
        url("./Inter/Inter-Regular.woff") format("woff");
    font-display: swap;
  } 
   
  @font-face { 
    font-family: 'Inter';
    font-style: normal;
    font-weight: 500;
    src: url("./Inter/Inter-Medium.woff2") format("woff2"),
