    font-display: swap;
  } 
   
  @font-face { 
    font-family: 'Inter';
    font-style: normal;
    font-weight: 900;
    src: url("./Inter/Inter-Black.woff2") format("woff2"),
        url("./Inter/Inter-Medium.woff") format("woff");
    font-display: swap;
  }